@import url(https://fonts.googleapis.com/css2?family=Grandstander&display=swap);
*{
  margin: 0;
  padding: 0;
}
html{
  overflow-x: hidden;
}
body {
  display: grid;
  place-items: center;
  font-family: 'Grandstander', cursive;
  background-color: #e0e0e0;
}

@media(max-width:576px){
  body{
    font-size:0.7em !important
  }
}

.app{
  margin-top:40px;
  display: flex;
  flex-direction: column;
  width: 70vw;
  min-height:90vh;
  margin-bottom: 30px;
}
.title{
  padding: 0.6em;
}
@media(max-width:576px){
  .app{
    width: 90vw;
    
  }
}
.search__container{
    display: flex;
    align-items: center;
    justify-content: center;
     flex:0.1 1;
    width: 100%;
    min-height:15vh;
    border: 1px solid black;
    background-color: #fff;
    border-radius: 5px;
}
.input__container{
    display: flex;
    height:45px;
    flex: 0.8 1;
}
input{
    border: 1px solid black;
    width:70%;
    padding: 10px;
    font-size:20px;
    border-radius: 5px;
}
.input__button{
    width: 6em;
    border-radius: 10px;
    font-size: 24px;
    margin-left:20px;
    border: none;
    cursor: pointer;
    background-color: #28a745;
    color: white;
  
}
.sidebar {
  border: 1px solid black;
  flex: 0.5 1;
  position: relative;
  border-radius: 5px;
  padding: 30px;
  background-color: #fff;
  margin-right: 10px;
}
.sidebar__list {
  margin-top: 30px;
  font-size: 1.6em;
}
ul li {
  list-style-type: none;
  padding: 10px;
}
.list__item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 6vh;
  font-size: 16px;
  cursor: pointer;
}
.list__item:hover {
  color: red;
}
button:disabled {
  background-color: #49a1ff;
  cursor: not-allowed;
}
.item__btn {
  background: rgb(214, 214, 221);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 6px;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
.item__btn:focus {
  outline: none;
  border: none;
}
.error {
  position: absolute;
  top: 40%;
  left: 35%;
}
@media(max-width:576px){
    .sidebar{
       margin-bottom: 1.1em;
       margin-left: 0;
       margin-right: 0;
    }

}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid black;
  border-color: black transparent black transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.results{
    display: flex;
    margin-top:10px;
   flex: 1 1;
}
@media(max-width:576px){
    .results{
        flex-direction: column;
    }

}
.nominations {
  border: 1px solid black;
  border-radius: 5px;
  flex: 0.5 1;
  position: relative;

  padding: 30px;
  background-color: #fff;
}
.nominations__title {
  position: absolute !important;
  left: 23%;
  color: gray;
  top: 50%;
}
.nominations__list {
  margin-top: 30px;
  font-size: 1.6em;
}
ul li {
  list-style-type: none;
  padding: 10px;
}
.list__item {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.nomination_item__btn {
  background: rgb(214, 214, 221);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 6px;
  border-radius: 5px;
  color: white;
  border: none;
  cursor: pointer;
  background-color: #dc3545;
}
.nomination_item__btn:focus {
  outline: none;
  border: none;
}
.share__buttons {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  bottom: 10%;
}
.share__button {
  width: 4em;
  height: 4em;
}
.share__button:hover {
  transform: scale(1.3);
}
@media(max-width:576px){
  .nominations{
    min-height: 30vh;
  }
    .share__buttons{
        position: unset;
        margin-top: 4em;
    }
    .list__item {
        margin-top:25px
      }
     .nominations__title{
       left: unset;
     }

}

